<template>
  <SlideEditor id="slide_editor">
    <template #params>
      <div class="col">
        SingleChoice params
      </div>
    </template>

    <template #controls>
      SingleChoice controls

      <button
          @click="addText"
      >
        Text (max 2)
      </button>
    </template>
  </SlideEditor>
</template>

<script>
import SlideEditor from "@/components/SlideEditor";
import {ADD_TEXT_ELEMENT} from "@/store/actions/action-types";
import Restricted from "@/components/mixins/Restricted";

export default {
  name: 'SingleChoice',
  components: {
    SlideEditor,
  },
  mixins: [Restricted],
  props: {},
  data() {
    return {
      restrictions: {
        TextObject: {min: 1, max: 2},
      }
    }
  },
  methods: {
    addText() {
      this.tryAction('TextObject', ADD_TEXT_ELEMENT, {x: 5, y: 5, width: 20, height: 10})
    },
  },
}
</script>

<style lang="scss" scoped>
</style>
