import {ADD_IMAGE_ELEMENT, ADD_TEXT_ELEMENT, ADD_YOUTUBE_ELEMENT} from "@/store/actions/action-types";
import {ADD_SLIDE_ELEMENT} from "@/store/mutations/mutation-types";

export default {
    [ADD_TEXT_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {component: 'TextObject', value: ''},
        })
    },
    [ADD_IMAGE_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {
                component: 'ImageObject', value: {
                    url: null,
                    fill: false,
                }
            },
        })
    },
    // eslint-disable-next-line no-unused-vars
    [ADD_YOUTUBE_ELEMENT]({commit}) {
        // commit(ADD_SLIDE_ELEMENT, {
        //     component: 'YouTubeObject',
        //     html: `<iframe src="https://www.youtube.com/embed/5P-ndTBfyeY?controls=0&autoplay=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
        // })
    },
}